import React from 'react';
import '../css/landing.css';
import LanderImage from '../assets/LanderImage.png';

class Lander extends React.Component{
    render(){
        return(
            <>
                <header>
                    <nav id="landerHeader" className="navbar navbar-expand-lg navbar-light py-3 px-md-5 mt-lg-5 bg-white-lg">
                        <a className="navbar-brand" href="./">Spidey Suite</a>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav ml-auto">
                                <li className="nav-item active mr-3">
                                    <a className="nav-link" href="#">Home <span className="sr-only">(current)</span></a>
                                </li>
                                <li className="nav-item active mr-3">
                                    <a className="nav-link" href="#feature">Features</a>
                                </li>
                                <li className="nav-item dropdown active mr-3">
                                    <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                        Products
                                    </a>
                                    <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                                        <a className="dropdown-item" href="#spideyboot">Spideyboot</a>
                                        <a className="dropdown-item" href="#spideydude">Spideydude</a>
                                        <a className="dropdown-item" href="#spideyAVRdude">SpideyAVRdude</a>
                                    <div className="dropdown-divider"></div>
                                        <a className="dropdown-item" href="#product">All Products</a>
                                    </div>
                                </li>
                                <li className="nav-item active mr-3">
                                    <a className="nav-link" href="#contact">Contact</a>
                                </li>
                                <li className="nav-item active mr-3">
                                    <a className="nav-link" href="https://github.com/spider-tronix/spidey-suite" title="Visit Github Repo">
                                        <svg className="navbar-nav-svg" style={{width:"20px",marginTop:"-1px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 499.36" focusable="false">
                                            <title>GitHub</title>
                                            <path d="M256 0C114.64 0 0 114.61 0 256c0 113.09 73.34 209 175.08 242.9 12.8 2.35 17.47-5.56 17.47-12.34 0-6.08-.22-22.18-.35-43.54-71.2 15.49-86.2-34.34-86.2-34.34-11.64-29.57-28.42-37.45-28.42-37.45-23.27-15.84 1.73-15.55 1.73-15.55 25.69 1.81 39.21 26.38 39.21 26.38 22.84 39.12 59.92 27.82 74.5 21.27 2.33-16.54 8.94-27.82 16.25-34.22-56.84-6.43-116.6-28.43-116.6-126.49 0-27.95 10-50.8 26.35-68.69-2.63-6.48-11.42-32.5 2.51-67.75 0 0 21.49-6.88 70.4 26.24a242.65 242.65 0 0 1 128.18 0c48.87-33.13 70.33-26.24 70.33-26.24 14 35.25 5.18 61.27 2.55 67.75 16.41 17.9 26.31 40.75 26.31 68.69 0 98.35-59.85 120-116.88 126.32 9.19 7.9 17.38 23.53 17.38 47.41 0 34.22-.31 61.83-.31 70.23 0 6.85 4.61 14.81 17.6 12.31C438.72 464.97 512 369.08 512 256.02 512 114.62 397.37 0 256 0z"></path>
                                        </svg>
                                    </a>
                                </li>
                            </ul>

                        </div>
                    </nav>
                </header>
                <section className="secFitHeight" id="home">
                    <div className="layerWrapper">
                        <div className="layerbgcont"></div>
                    </div>
                    <div className="sec_container">
                        <div className="ldg_wrapper container-fluid ">
                            <div className="row">
                                <div className="col-lg-6 col-md-12 col-sm-12 order-2">
                                    <div className="header-text-area">
                                        <h1>Add OTA update <br/>feature in minutes</h1>
                                        <h3>Working on adding OTA (Over The Air) programming feature to your AVR microcontroller and looking for a compatible products? <b>spidey suite</b> can come in handy.</h3>
                                        <div className="action-wrap">
                                            <a href="#feature"><button className="get_started_but">Explore More</button></a>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-lg-6 col-md-12 col-sm-12">
                                    <div className="ldg_img_box text-center">
                                        <img src={LanderImage} alt="Landing Laptop"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </>
        );
    }
    componentDidMount(){
        function scrollSpy(){
            console.log(document.documentElement.scrollTop);
            if(document.documentElement.scrollTop > 102){
                document.getElementById("landerHeader").classList.add("shadow");
                document.getElementById("landerHeader").classList.add("position-fixed");
                document.getElementById("landerHeader").classList.add("bg-white");
                document.getElementById("landerHeader").classList.remove("mt-lg-5");
            }
            else{
                document.getElementById("landerHeader").classList.add("mt-lg-5");
                document.getElementById("landerHeader").classList.remove("bg-white");
                document.getElementById("landerHeader").classList.remove("position-fixed");
                document.getElementById("landerHeader").classList.remove("shadow");
            }
        }
        scrollSpy();
        window.addEventListener('scroll',scrollSpy);
    }
}

export default Lander;